import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'tp-elem-cart-button',
  styleUrl: 'cart-button.scss',
  scoped: true,
})
export class TpElemCartButton {
  /**
   * O total de itens no carrinho
   */
  @Prop() cartCount: number;

  render() {
    const { cartCount } = this;

    return (
      <Host>
        <div class="cart-button">
          <ion-button
            color="brand"
            fill="clear"
            tp-no-padding
            href={`${process.env.WP_BASE_URL}/cart`}
          >
            Ir para o carrinho
          </ion-button>
          {cartCount && <span class="cart-button__count">{cartCount}</span>}
        </div>
      </Host>
    );
  }
}
