.cart-button {
  position: relative;
  width: fit-content;

  &__count {
    position: absolute;
    top: -8px;
    right: -8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--tp-color-warning);
    color: var(--tp-color-neutral-10);
    height: 24px;
    width: 24px;
    border-radius: 100px;
    font-size: 12px;
    font-weight: 600;
  }
}
